<<template>
  <div id="view-employee">
    <ul class="collection with-header">
      <li class="collection-header"><h4>{{ name }}</h4></li>
      <li class="collection-item">Employee ID: {{ employeeID }}</li>
      <li class="collection-item">Department: {{ dept }}</li>
      <li class="collection-item">Position: {{ position }}</li>
    </ul>
    <router-link class="btn grey" to="/">Back</router-link>
    <button @click="deleteEmployee" class="btn red">Delete</button>
  <div class="fixed-action-btn">
    <router-link v-bind:to="{name: 'edit-employee', params: {employeeID: employeeID}}" class="btn-floating btn-large red">
    <i class="fa fa-pencil"></i>
    </router-link>
  </div>
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-employee',
  data () {
    return {
      employeeID: null,
      name: null,
      dept: null,
      position: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('employees').where('employeeID', '==', to.params.employeeID).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.employeeID = doc.data().employeeID
          vm.name = doc.data().name
          vm.dept = doc.data().dept
          vm.position = doc.data().position
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData() {
      db.collection('employees').where('employeeID', '==', this.$route.params.employeeID).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.employeeID = doc.data().employeeID
          this.name = doc.data().name
          this.dept = doc.data().dept
          this.position = doc.data().position
        })
      })
    },
    deleteEmployee(){
      if(confirm('Are you sure?')){
        db.collection('employees').where('employeeID', '==', this.$route.params.employeeID).get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete()
            this.$router.push('/')
          })
        })
      }
    }
  }
}
</script>



